@use './variable' as *;

@mixin mobile {
    @media only screen and (max-width:$mobile-width){
        @content;
    }
}

@mixin tablet {
    @media only screen and (max-width:$tablet-width){
        @content;
    }
}


@mixin minipc {
    @media only screen and (max-width:$mini-pc-width){
        @content;
    }
}